<template>
  <div id="app">
    <div v-if="hasFilterData" class="row mb-4 filter">
      <div v-for="filter in filterData" :key="filter.name" class="col-md">
        <label :for="filter.name" class="form-label">
          {{ filter.label }}
        </label>
        <template v-if="filter.type === 'password'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            class="form-control input-password"
            :type="filter.type"
            :required="filter.required"
            :name="filter.name"
          />
          <a href="javascript:void(0)" class="show-pass"
            ><i class="fa fa-eye-slash"></i
          ></a>
        </template>
        <template v-if="filter.type === 'date'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            :type="filter.type"
            :required="filter.required"
            :name="filter.name"
          />
        </template>
        <template v-if="filter.type === 'datetime'">
          <datetime
            v-model.trim.lazy="filter.value"
            :type="filter.type"
            input-class="form-control"
            :input-id="filter.name"
            format="MM/dd/yyyy HH:MM a"
            autocomplete="off"
          ></datetime>
        </template>
        <template v-if="filter.type === 'paragraph'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            :type="filter.type"
            class="form-control"
            :required="filter.required"
            readOnly="true"
            :name="filter.name"
          />
        </template>
        <template v-if="filter.type === 'text'">
          <input
            :id="filter.name"
            v-model="filter.value"
            class="form-control"
            :type="filter.type"
            :required="filter.required"
            :name="filter.name"
          />
        </template>
        <template v-if="filter.type === 'textarea'">
          <textarea
            :id="filter.name"
            v-model="filter.value"
            class="form-control"
            :required="filter.required"
            :name="filter.name"
          ></textarea>
        </template>
        <template v-if="filter.type === 'select'">
          <multiselect
            v-model="filter.selectedValue"
            :options="filter.options"
            :searchable="false"
            :show-labels="false"
            label="label"
            track-by="label"
          ></multiselect>
        </template>
        <template v-if="filter.type === 'selectsearch'">
          <multiselect
            v-model="filter.value"
            :options="filter.options"
            :custom-label="nameWithLang"
            label="label"
            track-by="label"
          ></multiselect>
        </template>
        <template v-if="filter.type === 'multiselect'"
          ><multiselect
            v-model="filter.selectedValues"
            :options="filter.options"
            :multiple="true"
            :close-on-select="false"
            :preserve-search="true"
            label="label"
            track-by="label"
            :reset-after="false"
            group-values="groupOptions"
            group-label="groupLabel"
            :group-select="true"
            :hide-selected="true"
          ></multiselect>
        </template>
      </div>
      <div class="col-md-2">
        <div class="mb-3">
          <label class="form-label"> &nbsp; </label>
          <button
            class="form-control btn btn-primary"
            type="submit"
            @click.prevent="submitForm"
          >
            Филтрирай
          </button>
        </div>
      </div>
      <div v-if="hasActiveFilters" class="col-md-2">
        <div class="mb-3">
          <label class="form-label"> &nbsp; </label>
          <button
            class="form-control btn btn-danger"
            type="button"
            @click.prevent="resetFilters"
          >
            Изчисти
          </button>
        </div>
      </div>
    </div>

    <div class="row mb-4 sort">
      <span class="col-1 mr-4">Показване: </span>
      <b-select
        v-model="perPage"
        class="col-2 px-3"
        :options="totalPagesOptions"
        @change="setPerPage"
      ></b-select>
    </div>

    <div v-if="hasTableData">
      <b-table
        :items="items"
        :fields="fields"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        sort-icon-left
        :current-page="currentPage"
        no-sort-reset
        responsive="sm"
        @sort-changed="setSorting"
      >
        <!-- eslint-disable vue/no-v-html -->
        <template #cell()="data">
          <div v-html="data.value"></div>
        </template>
        <!-- eslint-enable vue/no-v-html -->
        <template #cell(Actions)="data">
          <a
            v-for="action in data.item.Actions"
            :key="action.key"
            :class="btnClass(action.key)"
            :href="action.url"
            style="margin: 0 1px"
          >
            <i :class="btnIconClass(action.key)"></i>
          </a>
        </template>
      </b-table>
      <b-pagination-nav
        v-model="currentPage"
        :number-of-pages="totalPages"
        base-url="#"
        prev-text="Предишна"
        next-text="Следваща"
        size="sm"
        class="my-0"
        @change="setPage"
      ></b-pagination-nav>
    </div>
  </div>
</template>

<script>
const config = {
  initCurrentPage: 1,
  initPerPage: 5,
  initSortBy: null,
  initSortDesc: false,
}

export default {
  name: 'App',
  data() {
    return {
      initFilterData: {},
      tableData: {},
      filterData: {},
      tableDataColumns: [],
      sortBy: config.initSortBy,
      sortDesc: config.initSortDesc,
      totalPages: null,
      perPage: config.initPerPage,
      currentPage: config.initCurrentPage,
      rows: null,
      totalPagesOptions: [
        { value: 5, text: '5' },
        { value: 10, text: '10' },
        { value: 15, text: '15' },
        { value: 20, text: '20' },
      ],
      requestedFilters: false,
    }
  },
  computed: {
    hasTableData() {
      return Object.keys(this.tableData).length !== 0
    },
    hasFilterData() {
      return this.tableData.length !== 0
    },
    hasActiveFilters() {
      return !this.$lodash.isEqual(this.filterData, this.initFilterData)
    },
    isPerPageInit() {
      return this.perPage == config.initPerPage
    },
    isCurrentPageInit() {
      return this.currentPage == config.initCurrentPage
    },
    isSortByInit() {
      return this.sortBy == config.initSortBy
    },
    isSortDescInit() {
      return this.sortDesc == config.initSortDesc
    },
    fields() {
      if (this.tableData.length !== 0) {
        return this.tableData.columns
      }
      return []
    },
    items() {
      if (this.tableData.length !== 0) {
        const items = this.tableData.rows.map((row) => ({
          objectId: row.objectId,
          ...(() => {
            const cells = row.cells.map((cell) =>
              cell.key !== 'actions'
                ? { [cell.key]: cell.text }
                : { Actions: cell.actions }
            )
            return Object.assign({}, ...cells)
          })(),
        }))
        return items
      }
      return []
    },
    query() {
      return this.$lodash.pickBy({
        currentPage: Number(this.currentPage),
        perPage: Number(this.perPage),
        sortBy: !this.isSortByInit ? this.sortBy : null,
        sort: !this.isSortByInit
          ? this.sortDesc === true
            ? 'desc'
            : 'asc'
          : null,
      })
    },
  },
  watch: {
    '$route.query': {
      handler(query, prevQuery) {
        this.currentPage = Number(query.currentPage || config.initCurrentPage)
        this.perPage = Number(query.perPage || config.initPerPage)
        this.sortBy = query.sortBy || config.initSortBy
        this.sortDesc =
          (query.sort === 'desc' ? true : false) || config.initSortDesc

        if (!this.$lodash.isEqual(query, prevQuery)) {
          this.submitForm()
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    async getData() {
      try {
        const { data } = await this.axios.get(window.jsonData.jsonUrlFilter)
        this.filterData = data
        this.initFilterData = this.$lodash.cloneDeep(this.filterData)

        if (!this.$lodash.isEmpty(this.$route.query)) {
          this.currentPage = Number(
            this.$route.query.currentPage || config.initCurrentPage
          )
          this.perPage = Number(this.$route.query.perPage || config.initPerPage)
          this.sortBy = this.$route.query.sortBy || config.initSortBy
          this.sortDesc =
            (this.$route.query.sort === 'desc' ? true : false) ||
            config.initSortDesc

          this.submitForm()
        } else {
          const apiUrl = window.jsonData
          const { data: tableData } = await this.axios.get(apiUrl.jsonUrlTable)

          this.tableData = tableData
          this.tableDataColumns = tableData.columns
            .filter((col) => col.sortable === true)
            .map((col) => col.key)
          this.totalPages = tableData.totalPages
          this.currentPage = Number(
            this.$route.query.currentPage || tableData.currentPage
          )
          this.perPage = Number(
            this.$route.query.perPage || tableData.numberOfRowsPerPage
          )
          this.rows = tableData.rows.length
          this.sortBy = this.$route.query.sortBy || tableData.orderColumn
          this.sortDesc =
            (this.$route.query.sort === 'desc' ? true : false) ||
            (tableData.orderDirection === 'desc' ? true : false)
        }
      } catch (e) {
        console.error('Error loading filter data and table data', '\n', e)
      }
    },
    async submitForm() {
      try {
        const additionalPayload = {
          currentPage: Number(this.currentPage),
          numberOfRowsPerPage: Number(this.perPage),
          orderColumn: !this.$lodash.isEmpty(this.$route.query.sortBy)
            ? this.$route.query.sortBy
            : this.sortBy,
          orderDirection: !this.$lodash.isEmpty(this.$route.query.sortBy)
            ? !this.$lodash.isEmpty(this.$route.query.sort)
              ? this.$route.query.sort
              : 'asc'
            : null,
        }

        const payload = this.$lodash
          .cloneDeep(this.filterData)
          .concat(additionalPayload)

        console.log('submitted with:', payload)

        if (!this.$lodash.isEmpty(this.filterData)) {
          this.requestedFilters = true
        }

        const apiUrl = window.jsonData

        const { data: newTableData } = await this.axios.post(
          apiUrl.jsonUrlTable,
          payload
        )
        console.log('returned:', newTableData)

        this.tableData = newTableData
        this.tableDataColumns = newTableData.columns
          .filter((col) => col.sortable === true)
          .map((col) => col.key)
        this.totalPages = newTableData.totalPages
        this.currentPage = newTableData.currentPage
        this.perPage = newTableData.numberOfRowsPerPage
        this.rows = newTableData.rows.length
        this.sortBy = newTableData.orderColumn || null
        this.sortDesc =
          newTableData.orderDirection != null
            ? newTableData.orderDirection === 'desc'
              ? true
              : false
            : null
      } catch (e) {
        console.error('Error submitting filter:', '\n', e)
      }
    },
    btnClass(type) {
      switch (type) {
        case 'view':
          return 'btn btn-success'
        case 'history':
          return 'btn btn-info'
        case 'edit':
          return 'btn btn-warning '
        case 'delete':
          return 'btn btn-danger'
        case 'rights':
          return 'btn btn-rights'
      }
    },
    btnIconClass(type) {
      switch (type) {
        case 'view':
          return 'mdi mdi-eye-outline'
        case 'history':
          return 'mdi mdi-progress-clock'
        case 'edit':
          return 'dripicons-pencil'
        case 'delete':
          return 'dripicons-trash'
        case 'rights':
          return 'mdi mdi-key-outline'
      }
    },
    filterColClass(col) {
      return `col-md-${col}`
    },
    setQuery() {
      this.$router.push({ query: this.query }).catch(() => {})
    },
    setPage(page) {
      this.currentPage = Number(page)
      this.setQuery()
    },
    setPerPage(value) {
      this.perPage = Number(value)
      this.setQuery()
    },
    setSorting(ctx) {
      this.sortBy = ctx.sortBy
      this.sortDesc = ctx.sortDesc
      this.setQuery()
    },
    resetFilters() {
      if (this.requestedFilters === true) {
        this.submitForm()
      }
      this.filterData = this.$lodash.cloneDeep(this.initFilterData)
      this.requestedFilters = false
    },
  },
}
</script>
